.about {
    width: 100%;
    height: 90vh;
    display: flex;
    flex-direction: column;
    background-color: #f3eeee;
}

.aboutTop {
    width: 100%;
    height: 30%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: Sylfaen, serif;
}

.aboutBottom {
    display: flex;
    flex-direction: column;
    height: 55%;
    align-items: center;
    margin-top: 50px;
}

.aboutBottom p {
    width: 1200px;
    text-align: center;
}

.about .aboutBottom h1 {
    font-weight: 400;
    font-size: 70px;
    color: black;
    height: 30px;
    display: flex;
    justify-content: center;
    transform: translateY(-40px);
    font-family: Sylfaen, serif;

}